.header {
  height: 5rem;
  border-bottom: 1px solid var(--color-grey-dark-1);
  background-color: #fff;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  padding: 3rem 1.5rem;

  .img-container {
    img {
      height: 3rem;
      width: 3rem;
      border-radius: 50%;
    }
  }
}
